"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import Navbar from "@/app/components/Navbar";
import { Card, CardContent, CardHeader, CardTitle } from "@/app/components/ui/card";
import { Button } from "@/app/components/ui/button";
import { Plus } from "lucide-react";
import AddItemForm from "./components/AddItemForm";
import * as api from "@/lib/api";
import type { ChecklistItemDto } from "./types";

export default function ChecklistPage() {
    const params = useParams();
    const groupId = params.groupId as string;
    const [items, setItems] = useState<ChecklistItemDto[]>([]);
    const [loading, setLoading] = useState(true);
    const [isAddOpen, setIsAddOpen] = useState(false);
    const [error, setError] = useState<string | null>(null);

    const load = async () => {
        try {
        setLoading(true);
        setError(null);
        const data = await api.fetchChecklist(groupId);
        setItems(Array.isArray(data) ? data : []);
        } catch (err: any) {
        console.error(err);
        setError(err.message || "Błąd ładowania checklisty");
        } finally {
        setLoading(false);
        }
    };

    useEffect(() => {
        if (groupId) load();
    }, [groupId]);

    const handleAdd = async (text: string) => {
        try {
        const newItem = await api.addChecklistItem(groupId, text);
        // backend returns created item; odśwież listę lub dołącz nowy element
        setItems(prev => [newItem, ...prev]);
        setIsAddOpen(false);
        } catch (err: any) {
        console.error(err);
        alert(err.message || "Nie udało się dodać pozycji");
        }
    };

    const handleToggle = async (itemId: string, newChecked: boolean) => {
        setItems(prev => prev.map(i => (i.id === itemId ? { ...i, isChecked: newChecked } : i)));
        try {
        await api.toggleChecklistItemStatus(itemId, newChecked);
        } catch (err: any) {
            console.error(err);
            setItems(prev => prev.map(i => (i.id === itemId ? { ...i, isChecked: !newChecked } : i)));
            alert(err.message || "Nie udało się zapisać zmian");
        }
    };

    return (
        <>
        <Navbar />
        <main className="min-h-screen pt-24 pb-16 bg-gradient-soft">
            <div className="container mx-auto px-4 max-w-4xl">
            <Card className="mb-6">
                <CardHeader>
                <CardTitle className="flex items-center justify-between">
                    <span>Lista kontrolna</span>
                    <div className="flex items-center gap-2">
                    <Button onClick={() => setIsAddOpen(v => !v)} className="gap-2">
                        <Plus className="w-4 h-4" /> Dodaj pozycję
                    </Button>
                    </div>
                </CardTitle>
                </CardHeader>
                <CardContent>
                <p className="text-sm text-muted-foreground">
                    Dodawaj elementy, a zaznaczenia będą zapisywane tylko dla Ciebie.
                </p>
                </CardContent>
            </Card>

            <div className="mb-6">
                {isAddOpen && (
                <AddItemForm onSubmit={handleAdd} onCancel={() => setIsAddOpen(false)} />
                )}
            </div>

            <ChecklistList
                items={items}
                loading={loading}
                onToggle={handleToggle}
                onRefresh={load}
                error={error}
            />
            </div>
        </main>
        </>
    );
}